(function() {
  'use strict';
  var WebService;

  WebService = (function() {
    function WebService($http1) {
      this.$http = $http1;
      this.baseUrl = "/api/v1/";
    }

    WebService.prototype.getGreeting = function() {
      return this.$http.get(this.baseUrl + "greet");
    };

    return WebService;

  })();

  angular.module("personalized_ssk.WebService", [], function($provide) {
    return $provide.factory("webService", [
      "$http", function($http) {
        return new WebService($http);
      }
    ]);
  });

}).call(this);



(function() {
  'use strict';
  var personalized_ssk;

  personalized_ssk = angular.module('personalized_ssk', ['ngResource', 'ngSanitize', 'ngAnimate', 'ngRoute', 'personalized_ssk.WebService']);

  personalized_ssk.config([
    '$routeProvider', function($routeProvider) {
      return $routeProvider.when('/home', {
        templateUrl: 'app/views/main.html',
        controller: 'MainCtrl'
      }).when('/signed_in_home', {
        templateUrl: 'views/signed_in_home.html',
        controller: 'MainCtrl'
      }).when('/send_invite', {
        templateUrl: 'views/send_invite.html',
        controller: 'MainCtrl'
      }).when('/resources', {
        templateUrl: 'views/resources.html',
        controller: 'MainCtrl'
      }).when('/about', {
        templateUrl: 'views/about.html',
        controller: 'MainCtrl'
      }).when('/settings', {
        templateUrl: 'views/settings.html',
        controller: 'MainCtrl'
      }).when('/contact_us', {
        templateUrl: 'views/contact_us.html',
        controller: 'MainCtrl'
      }).when('/', {
        templateUrl: 'app/views/main.html',
        controller: 'MainCtrl'
      });
    }
  ]);

  personalized_ssk.factory('myInterceptor', [
    function() {
      var myInterceptor;
      myInterceptor = {
        'request': function(config) {
          return config;
        },
        'requestError': function(rejection) {
          var d;
          if (resp.status === 401) {
            d = $q.defer();
            storageService.logout();
            $location.url("/");
            $scope.$broadcast("notify", {
              message: "Sorry, an authentication error has occurred."
            });
            return d.promise;
          }
          $q.reject(resp);
          if (canRecover(rejection)) {
            return responseOrNewPromise;
          }
          return $q.reject(rejection);
        },
        'response': function(response) {
          return response;
        },
        'responseError': function(rejection) {
          if (canRecover(rejection)) {
            return responseOrNewPromise;
          }
          return $q.reject(rejection);
        }
      };
      return myInterceptor;
    }
  ]);

  personalized_ssk.config([
    '$httpProvider', function($httpProvider) {
      $httpProvider.interceptors.push('myInterceptor');
    }
  ]);

}).call(this);



(function() {
  'use strict';
  var MainCtrl,
    bind = function(fn, me){ return function(){ return fn.apply(me, arguments); }; };

  MainCtrl = (function() {
    function MainCtrl($scope, webService) {
      this.$scope = $scope;
      this.webService = webService;
      this.error = bind(this.error, this);
      this.success = bind(this.success, this);
      this.setup();
    }

    MainCtrl.prototype.setup = function() {
      this.$scope.awesomeThings = ['HTML5 Boilerplate', 'AngularJS', 'Karma'];
      return this.getData();
    };

    MainCtrl.prototype.getData = function() {
      var promise;
      promise = this.webService.getGreeting();
      return promise.then(this.success, this.error);
    };

    MainCtrl.prototype.success = function(response) {
      return this.$scope.fname = response.data.fname;
    };

    MainCtrl.prototype.error = function(response) {
      return this.$scope.message = "Error!";
    };

    return MainCtrl;

  })();

  MainCtrl.$inject = ["$scope", "webService"];

  angular.module("personalized_ssk").controller("MainCtrl", MainCtrl);

}).call(this);



